import React, { FC } from 'react';
import styled from '../Style/typed-components';

const Container = styled.input`
  border: none;
  border-bottom: 2px solid ${(props) => props.theme.greyColor};
  font-size: 20px;
  width: 100%;
  padding-bottom: 10px;
  font-weight: 500;
  transition: border-bottom 0.1s linear;
  &:-webkit-autofill {
    box-shadow: 0 0 0px 1000px white inset !important;
  }
  &:focus {
    border-bottom-color: #2c3e50;
    outline: none;
  }
  &::placeholder {
    color: ${(props) => props.theme.greyColor};
    font-weight: 300;
  }
`;

interface IProps {
  placeholder?: string;
  name?: string;
  type?: string;
  required?: boolean;
  value: string;
  onChange: any;
  className?: string;
  disabled?: boolean;
}

const Input: FC<IProps> = ({
  placeholder = '',
  name = '',
  type = 'text',
  required = true,
  value,
  onChange,
  className,
  disabled = false,
}) => (
  <Container
    type={type}
    className={className}
    name={name}
    placeholder={placeholder}
    value={value}
    required={required}
    onChange={onChange}
    disabled={disabled}
  />
);

export default Input;
